*, html, body {
  margin: 0;
  padding: 0;
}

.header {
  background-image: url("header-bg.5ca04a71.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
  margin: 0;
}

.header--margin {
  padding-top: 25px;
}

.top-bar {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  column-gap: 20px;
  padding-left: 20px;
  padding-right: 20px;
  display: grid;
}

.top-bar_link {
  width: 64px;
}

.top-bar_icons {
  justify-content: end;
  align-items: center;
  gap: 24px;
  display: -ms-flexbox;
  display: flex;
}

.top-bar_icons--phone, .top-bar_icons--menu {
  width: 24px;
  height: 24px;
  display: block;
}
